import { Injectable } from '@angular/core';
import { CanActivate, Router} from '@angular/router';
import { isLoggedin } from './is-loggedin';
import {CookieService} from './../services/cookie.service';
import {FeatureAuthService} from './../services/feature-access.service';
@Injectable()
export class AnalyticsGuard implements CanActivate {

	constructor(private router: Router, private featureAuth : FeatureAuthService, private _cookieService :CookieService) {
		//this.getAccess();
	}

  	canActivate() {
      let storage : any = this._cookieService.readCookie('storage');
      if(storage) {
        storage = JSON.parse(storage);
        let current_usage : any = storage.company.current_usage;
        let current_limit : any = storage.company.current_limit;
        if( (current_limit.leads != -1 && current_usage.leads >= current_limit.leads ) || ( current_limit.traffic != -1 && current_usage.traffic >= current_limit.traffic))
          return false;
        else
          return <boolean>this.featureAuth.getfeatureAccess('analytics');
      }
  	}
	 /* canActivate() {
  return true;//this.featureAuth.getfeatureAccess("analytics");
 	}*/


}



